<script land="script" scoped>
import { getOrder } from '@/store/module.js'
import { getOrderInvoice } from '@/store/module.js'
import { pageRouter } from '@/common/js/mixin.js'
import { ImagePreview } from 'vant'
export default {
  name: 'OrderDetail',
  components: {
  },
  mixins: [pageRouter],
  data() {
    return {
      OrderLogList: [], // 操作日志信息
      expresses: [], // 物流信息
      activities: [],
      checked: true,
      orderId: '', // 订单id
      OrderInvoice: '',
      Order: '',
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"'
    }
  },
  created() {
    this.orderId = this.$store.state.member.orderId
    console.log(this.orderId)
    this.getOrder()
    this.getOrderInvoice()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    showMore1(detailIndex) {
      this.Order.detailList[detailIndex].check = !this.Order.detailList[detailIndex].check
      this.Order.detailList.splice(detailIndex, 1, this.Order.detailList[detailIndex])
    },
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    goBack() {
      this.$router.back()
    },
    // 获取订单发票信息
    getOrderInvoice() {
      getOrderInvoice.data = { e_order_id: this.orderId }
      this.asyncApiModule(getOrderInvoice).then(res => {
        this.OrderInvoice = res
      })
        .catch(() => {
        })
    },
    // 获取订单信息
    getOrder() {
      getOrder.data = { e_order_id: this.orderId }
      this.asyncApiModule(getOrder).then(res => {
        // this.$bus.$emit('change')
        for (let index = 0; index < res.detailList.length; index++) {
          if (res.detailList[index].dsbs === 'JD') {
            res.detailList[index].dsbs = '京东'
          } else if (res.detailList[index].dsbs === 'KJXY') {
            res.detailList[index].dsbs = '框架协议'
          } else if (res.detailList[index].dsbs === 'SN') {
            res.detailList[index].dsbs = '苏宁'
          }
        }
        this.Order = res
        for (let i = 0; i < this.Order.detailList.length; i++) {
          this.Order.detailList[i].check = false
        }
      })
        .catch(() => {
        })
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd'
      })
    },
    goorderexpress(title) {
      this.$router.push({
        name: 'OrderExpress',
        params: {
          title: title,
          beiz: this.Order.beiz
        }
      })
    },
    goorderevaluation() {
      this.$router.push({
        name: 'OrderEvaluation'
      })
    },
    goorderserviceapply() {
      this.$router.push({
        name: 'OrderServiceApply'
      })
    }
  }
}
</script>

